.App {
  text-align: center;
}

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: opacity 0.5s;
}

.watched {
  width: 15vw;
  /* height: 20vh; */
  position: fixed;
  right: 10px;
  bottom: 100px;
  border: 1px solid #000;
  background: rgba(255,255,255,0.5);
}

.watched p {
  font-size: 2vw;
}